





















import Vue from 'vue';

function escapeRegExp(s: string): string {
    return s.replace(/[-/\\^$*+?.()|[\]{}]/g, '\\$&')
}

export default Vue.extend({
    name: 'Word',
    props: {
        langCode: String,
        words: Array,
        meaning: String
    },
    data() {
        return {
            link: "",
            language: ""
        };
    },
    methods: {
        async findLink() {
            for (const word of this.words as string[]) {
                const response = await this.$http.get("https://en.wiktionary.org/w/api.php", {
                    params: {
                        origin: "*", // Enable cross-origin resource sharing
                        format: "json", // Output as json

                        // Get language name and word code
                        action: "expandtemplates",
                        text: "{{link|" + this.langCode + "|" + word + "}}",
                        prop: "wikitext"
                    }
                });

                const wikitext = response.data.expandtemplates.wikitext;
                if (wikitext.startsWith("<strong class=\"error\">")) {
                    // Unknown language
                    return;
                }

                const regex = /\[\[(.+)#(x3a;)?(.+?)(\/(.+))?\|/
                const match = regex.exec(wikitext);

                if (match === null) {
                    // Something happened
                    continue;
                }

                const wordCode = match[5] || match[1];
                this.language = match[3];

                const link = word.startsWith("*") ? "Reconstruction:" + this.language + "/" + wordCode : wordCode;

                const response2 = await this.$http.get("https://en.wiktionary.org/w/api.php", {
                    params: {
                        origin: "*", // Enable cross-origin resource sharing
                        format: "json", // Output as json

                        // Get page content
                        action: "query",
                        prop: "revisions",
                        titles: link,
                        rvslots: "*",
                        rvprop: "content",
                        formatversion: "2"
                    }
                });

                const page = response2.data.query.pages[0];
                if (page.missing) {
                    // Unknown word
                    continue;
                }

                const content = page.revisions[0].slots.main.content;
                
                const languageRegex = new RegExp("==" + this.language + "==.*?(?=(\n==[^=])|$)", "gs");
                const languageMatch = languageRegex.exec(content);

                if (languageMatch === null) {
                    // Unknown word
                    continue;
                }

                const languageSection = languageMatch[0]; // Section of page dedicated to the selected language

                // Check if diacritics are correct
                if (word.replace("*", "") != wordCode) {
                    const headRegex = new RegExp("(head=|\\|)" + escapeRegExp(word) + "(\\||})");
                    if (!headRegex.exec(languageSection)) {
                        // Unknown word
                        continue;
                    }
                }

                // We found a match!
                this.link = "/dictionary/" + this.langCode + "/" + word
                return;
            }
        }
    },
    mounted() {
        this.findLink();
    }
});
