<template>
    <div class="dictionary">
        <h1 v-if="langName">{{ langName }}: {{ word }}</h1>
        <p v-if="error" class="error">{{ error }}</p>
        <template v-if="trees.length > 0">
        <template v-for="(tree, index) in trees">
            <h2 :key="index">Etymology {{index + 1}}</h2>
            <WordTree :key="tree.word.words[0] + '-' + index" :language="tree.language" :word="tree.word" :parents="tree.parents" :children="tree.children" :extra="tree.extra" :meaning="tree.meaning"></WordTree>
        </template>
        </template>
        <div class="loader" v-if="trees.length == 0 && !error"></div>
    </div>
</template>

<script>
    // import router from '../router';
    import WordTree from '@/components/WordTree.vue';

    class StringReader {
        constructor(text) {
            this.text = text;
            this.index = 0;
        }
        remaining() {
            return this.text.substr(this.index);
        }
        advance(count) {
            this.index += count;
        }
    }

    function escapeRegExp(s) {
        return s.replace(/[-/\\^$*+?.()|[\]{}]/g, '\\$&')
    }

    export default {
        name: 'Page2',
        components: {
            WordTree
        },
        data () {
            return {
                lang: "en",
                word: "hello",
                langName: undefined,
                wordCode: undefined,
                trees: [],
                error: undefined
            }
        },
        created() {
            this.load();
        },
        watch: {
            '$route'() {
                this.load();
            }
        },
        methods: {
            errorNoConnection() {
                this.error = "Error: could not connect to the Wiktionary API";
            },
            errorUnknwonLanguageCode(code) {
                this.error = "Error: unknown language code '" + code + "'";
            },
            errorUnknwonLanguageWord(language, word) {
                this.error = "Error: unknown " + language + " word '" + word + "'";
            },
            errorNoEtymology(word) {
                this.error = "Error: no etymology found for '" + word + "'";
            },
            createInflectionTree(text) {
                const inflectionRegex = /{{.*of(\|[^|]+=[^|]+)*(\|[^|=}]*)?(\|([^|=}]+))(\|[^|}]*)*}}/gm;

                let inflectionMatch = inflectionRegex.exec(text);
                if (!inflectionMatch) {
                    // No etymology or inflections found
                    return false;
                }

                const tree = [];
                const usedWords = [];

                for (; inflectionMatch; inflectionMatch = inflectionRegex.exec(text)) {
                    if (usedWords.includes(inflectionMatch[4])) {
                        continue;
                    }
                    
                    usedWords.push(inflectionMatch[4]);
                    tree.push({
                        word: {
                            language: this.langName,
                            langCode: this.lang,
                            words: [inflectionMatch[4]]
                        }
                    });
                }

                this.trees.push({
                    parents: tree,
                    word: this.createRootWord(text)
                });
                return true;
            },
            createEtymologyTree(text, isRoot, siblings) {
                const output = [];

                let lastType;
                let remaining = text.remaining();

                // 3-word compound
                const confix = /{{confix\|[^|]*\|([^|]*)\|([^|]*)\|([^|]*)}}/;
                const matchConfix = confix.exec(remaining);
                if (matchConfix) {
                    const word1 = matchConfix[1] + "-";
                    const word2 = matchConfix[2];
                    const word3 = "-" + matchConfix[3];

                    output.push({
                        word: {
                            langCode: this.lang,
                            words: [word1]
                        }
                    });

                    output.push({
                        prefix: "+",
                        word: {
                            langCode: this.lang,
                            words: [word2]
                        }
                    });

                    output.push({
                        prefix: "+",
                        word: {
                            langCode: this.lang,
                            words: [word3]
                        }
                    });

                    // Advance text
                    lastType = "compound";
                    text.advance(matchConfix.index + matchConfix[0].length);
                    remaining = text.remaining();
                }

                // 2-word compound
                const compound = /{{(compound|suffix|suf|prefix)\|[^|]*\|([^|]*)(\|t1=[^|]*)?\|([^|]*)}}/;
                const matchCompound = compound.exec(remaining);
                if (matchCompound) {
                    const type = matchCompound[1];
                    let word1 = matchCompound[2];
                    let word2 = matchCompound[4];

                    if (type == "suffix" || type == "suf") {
                        if (!word2.startsWith("-")) {
                            word2 = "-" + word2;
                        }
                    } else if (type =="prefix" && !word1.endsWith("-")) {
                        word1 = word1 + "-";
                    }

                    output.push({
                        word: {
                            langCode: this.lang,
                            words: [word1]
                        }
                    });

                    output.push({
                        prefix: "+",
                        word: {
                            langCode: this.lang,
                            words: [word2]
                        }
                    });

                    // Advance text
                    lastType = "compound";
                    text.advance(matchCompound.index + matchCompound[0].length);
                    remaining = text.remaining();
                }

                const regex = /{{((m|cog)|((inh|der|bor)\|[^|}]*))\|([^|}]*)(\|([^|}]*))?(\|[^|}]*)??(\|([^|}]*))??(\|(t|pos)=([^|}]*))?}}/;

                let match = regex.exec(remaining);
                if (match === null) {
                    return output.length > 0 ? output : undefined;
                }

                for (; match; match = regex.exec(remaining)) {
                    // Check for periods
                    let until = remaining.substr(0, match.index);
                    const periods = /\.[ \n<](?!.*\.[ \n<])/m.exec(until);
                    if (periods) { // TODO: Make sure this doesn't include arguments from templates
                        if (!isRoot) {
                            // New line! return to root
                            return output.length == 0 ? undefined : output;
                        } else {
                            until = until.substr(periods.index);
                            lastType = undefined;
                        }
                    }

                    const or = output.length > 0 && (until.includes(" or ") || until.includes(" or\n") || until.toLowerCase().includes("alternativ"));
                    const plus = output.length > 0 && (until.includes(" + ") || until.includes(" +\n") || until.toLowerCase().includes("by adding"));
                    const and =  until.includes(",") && lastType !== undefined;
                    const from = !or && !plus && (until.toLowerCase().includes("from ") || until.toLowerCase().includes(" of "));
                    const allfrom = from && until.includes(" all from ");

                    if (output.length > 0 && from) {
                        const parents = this.createEtymologyTree(text, false, output);
                        if (parents !== undefined) {
                            if (allfrom) {
                                for (const word of output) {
                                    word.parents = parents;
                                }
                            } else {
                                output[output.length - 1].parents = parents;
                            }

                            lastType = undefined;
                            remaining = text.remaining();
                            continue;
                        }
                    }

                    // Advance text
                    text.advance(match.index + match[0].length);
                    remaining = text.remaining();

                    // Ignore displacement and equivilants and whences
                    if (until.toLowerCase().includes("displaced") || until.toLowerCase().includes("see ") || until.toLowerCase().includes(" equivalent ") || until.toLowerCase().includes("modern ") || until.toLowerCase().includes("whence ") || until.toLowerCase().includes("related ") || until.toLowerCase().includes("verbs in ") || until.toLowerCase().includes("such as ") || until.toLowerCase().includes("same phenomenon ")) {
                        lastType = undefined;
                        continue;
                    }

                    if (!plus && !or && !from && !and) {
                        continue;
                    }

                    // Check type
                    const type = match[4] || match[2];

                    if (type === "cog") {
                        lastType = "cog";
                        continue;
                    } else if (type === "m") {
                        if (until.toLowerCase().includes("cognate")) {
                            lastType = "cog";
                        }
                        if (lastType == "cog") {
                            continue;
                        }
                        if (from || output.length == 0 || lastType !== undefined) {
                            lastType = type;
                        } else {
                            lastType = undefined;
                            continue;
                        }
                    } else {
                        lastType = type;
                    }

                    // Form word
                    const langCode = match[5];
                    let word = match[7] || match[10];
                    let meaning = match[13] || match[10];
                    if (meaning == word) {
                        meaning = undefined;
                    }
                    if (match[12] == "t") {
                        meaning = '“' + meaning + '”'
                    }

                    if (word !== undefined) {
                        word = word.replace(/\[\[/g, "");
                        word = word.replace(/\]\]/g, "");
                    }

                    if (!word || word == "-") {
                        continue;
                    }

                    if (meaning !== undefined) {
                        meaning = meaning.replace(/\[\[/g, "");
                        meaning = meaning.replace(/\]\]/g, "");
                        meaning = meaning.replace(/''/g, "");

                        if (meaning.startsWith("tr=") || meaning.startsWith("*")) {
                            meaning = undefined;
                        }
                    }
                    
                    if (!or && !plus && !from && output.length > 0 && !output[output.length - 1].word.meaning && output[output.length - 1].word.langCode == langCode) {
                        output[output.length - 1].word.words.push(word);
                        output[output.length - 1].word.meaning = meaning;
                    } else if (from || or || plus || and) {
                        output.push({
                            prefix: or ? "or" : plus ? "+" : undefined,
                            word: {
                                langCode: langCode,
                                words: [word],
                                meaning: meaning
                            }
                        })
                    }
                }

                return output.length == 0 ? undefined : output;
            },
            createRootWord(text) {
                return {
                    langCode: this.lang,
                    words: [this.word]
                };
            },
            load() {
                this.trees = [];
                this.lang = this.$route.params.lang;
                this.word = this.$route.params.word;
                document.title = this.word;

                // Reset values
                this.langName = undefined;
                this.wordCode = undefined;
                this.error = undefined;

                this.$http.get("https://en.wiktionary.org/w/api.php", {
                    params: {
                        origin: "*", // Enable cross-origin resource sharing
                        format: "json", // Output as json

                        // Get language name and word code
                        action: "expandtemplates",
                        text: "{{link|" + this.lang + "|" + this.word + "}}",
                        prop: "wikitext"
                    }
                }).then((response) => {
                    const wikitext = response.data.expandtemplates.wikitext;
                    if (wikitext.startsWith("<strong class=\"error\">")) {
                        this.errorUnknwonLanguageCode(this.lang);
                        return;
                    }

                    const regex = /\[\[(.+)#(x3a;)?(.+?)(\/(.+))?\|/
                    const match = regex.exec(wikitext);

                    this.wordCode = match[5] || match[1];
                    this.langName = match[3];

                    const link = this.word.startsWith("*") ? "Reconstruction:" + this.langName + "/" + this.wordCode : this.wordCode;

                    return this.$http.get("https://en.wiktionary.org/w/api.php", {
                        params: {
                            origin: "*", // Enable cross-origin resource sharing
                            format: "json", // Output as json

                            // Get page content
                            action: "query",
                            prop: "revisions",
                            titles: link,
                            rvslots: "*",
                            rvprop: "content",
                            formatversion: "2"
                        }
                    }).then((response) => {
                        const page = response.data.query.pages[0];
                        if (page.missing) {
                            this.errorUnknwonLanguageWord(this.langName, this.word);
                            return;
                        }

                        const content = page.revisions[0].slots.main.content;
                        
                        const languageRegex = new RegExp("==" + escapeRegExp(this.langName) + "==.*?(?=(\n==[^=])|$)", "gs");
                        const languageMatch = languageRegex.exec(content);

                        if (languageMatch === null) {
                            this.errorUnknwonLanguageWord(this.langName, this.word);
                            return;
                        }

                        const languageSection = languageMatch[0]; // Section of page dedicated to the selected language

                        // Check if diacritics are correct
                        const headRegex = new RegExp("(head=|\\|)" + escapeRegExp(this.word) + "(\\||})", "g");
                        if (this.word.replace("*", "") != this.wordCode && !headRegex.exec(languageSection)) {
                            this.errorUnknwonLanguageWord(this.langName, this.word);
                            return;
                        }
                        headRegex.lastIndex = 0;

                        const etymologyRegex = /(?<=Etymology( [0-9])?=+\n)(.*?)(?=(=+Etymology)|$)/gs;

                        this.trees = [];

                        let etymologyMatch = etymologyRegex.exec(languageSection);
                        if (etymologyMatch === null) {
                            if (!this.createInflectionTree(languageSection)) {
                                this.errorNoEtymology(this.word);
                            }
                            return;
                        }

                        let foundSection = false;
                        const firstSectionRegex = /.*?(?=(==)|$)/s;

                        for (; etymologyMatch; etymologyMatch = etymologyRegex.exec(languageSection)) {
                            const fullSection = etymologyMatch[0];

                            if (this.word.replace("*", "") != this.wordCode && !headRegex.exec(fullSection)) {
                                // Incorrect diacritics
                                headRegex.lastIndex = 0;
                                continue;
                            }
                            headRegex.lastIndex = 0;

                            const etymology = firstSectionRegex.exec(fullSection)[0]; // Get etymology section

                            // TODO: make etymology tree
                            const tree = this.createEtymologyTree(new StringReader(etymology), true, []);
                            if (tree !== undefined) {
                                foundSection = true;
                                this.trees.push({
                                    parents: tree,
                                    word: this.createRootWord(fullSection)
                                });
                            } else {
                                foundSection = this.createInflectionTree(fullSection) || foundSection;
                            }
                        }

                        if (!foundSection) {
                            this.errorNoEtymology(this.word);
                        }
                    });
                }).catch(this.errorNoConnection);
            }
        }
    }
</script>

<style lang="scss">

.loader {
  border: 8px solid #f3f3f3; /* Light grey */
  border-top: 8px solid grey; /* Blue */
  border-radius: 50%;
  width: 40px;
  height: 40px;
  animation: spin 1s linear infinite;
  margin: 0 auto;
}

@keyframes spin {
  0% { transform: rotate(0deg); }
  100% { transform: rotate(360deg); }
}

p.error {
    color: red;
}

.dictionary > .tree {
    margin-bottom: 50px;
}

</style>
